import React from "react";
import {Button, Card, Table , Row , Col , Divider} from "antd";
import IntlMessages from "util/IntlMessages";
import { PlusCircleFilled, DeleteOutlined , EditOutlined , EyeFilled} from "@ant-design/icons";
import { connect } from "react-redux";
import { fetchStudents } from "../../../appRedux/actions/Students";


const columns = [{
  title: <IntlMessages id="columns.name"/>,
  dataIndex: 'name',
  align:'right'
}, {
  title: <IntlMessages id="columns.mobile"/>,
  dataIndex: 'mobile',
  align:'right'
},{
  title: <IntlMessages id="columns.status"/>,
  dataIndex: 'status',
  align:'right'
},{
  title: <IntlMessages id="columns.operation"/>, dataIndex: '', key: 'x', render: ({id}) => (
    <span>
      <Button dir="rtl" type="primary" icon={<EditOutlined />}/>
      <Divider type="vertical"/>
      <Button dir="rtl" type="primary" icon={<EyeFilled />}/>
    </span>
  ),
 align:'right'
}];


const data = [];
for (let i = 0; i < 46; i++) {
  data.push({
    key: i,
    name: `Edward King ${i}`,
    mobile: 32,
    address: `London, Park Lane no. ${i}`,
  });
}

class Students extends React.Component {
  state = {
    selectedRowKeys: [], // Check here to configure the CRM column
    loading: false,
  };
  start = () => {
    this.setState({loading: true});
    // ajax request after empty completing
    setTimeout(() => {
      this.setState({
        selectedRowKeys: [],
        loading: false,
      });
    }, 1000);
  };

  componentDidMount() {
    this.props.fetchStudents()
    this.setState({ loading:false })
  }

  onSelectChange = (selectedRowKeys) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  render() {
    console.log(this.props.students)
    const {loading, selectedRowKeys} = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;
    return (
      <Row>
       <Col span={24}>
        <Card title={<h1><IntlMessages id="sidebar.Students"/></h1>}>
            <Button size="large" icon={<PlusCircleFilled />}  type="primary" />
            <Button  size="large" icon={<DeleteOutlined/>} disabled={!hasSelected} type="danger" loading={loading} />
          <div className="gx-mb-3">
            <span style={{marginLeft: 8}}>
              {hasSelected ? `تم تحديد ${selectedRowKeys.length} عناصر` : ''}
            </span>
          </div>
          <Table direction="rtl" bordered={true} className="gx-table-responsive" rowSelection={rowSelection} columns={columns} dataSource={this.props.students}/>
        </Card>
      </Col>
    </Row>
    );
  }
}

function mapStateToProps(state) {
  return {
    students: state.students,
  };
}
export default connect(mapStateToProps, { fetchStudents })(Students)
